import React, {useState, useEffect} from "react";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableFooter from "@material-ui/core/TableFooter";
import TableHead from "@material-ui/core/TableHead";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import TableSortLabel from "@material-ui/core/TableSortLabel";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import PlaylistPlayIcon from "@material-ui/icons/PlaylistAdd";
import CardEditMenu from './CardEditMenu.js'
import SearchBox from '../../../../../components/SearchBox/index';
import {useSelector} from 'react-redux';
import {useHistory} from 'react-router-dom';
import CircularProgress from '@material-ui/core/CircularProgress';
import {base_url, $http} from "config";
import {Modal, ModalHeader} from 'reactstrap';
import CloseIcon from '@material-ui/icons/Close';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import Button from '@material-ui/core/Button';
import { BlockPicker } from 'react-color';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import IntlMessages from 'util/IntlMessages';
import SweetAlert from 'react-bootstrap-sweetalert'

const columnData = [
  {id: "id", align: false, disablePadding: true, label: "ID"},
  {id: "department_name", align: true, disablePadding: false, label: "Department"},
  {id: "rooms_count", align: true, disablePadding: false, label: "Rooms"},
  {id: "colour", align: true, disablePadding: false, label: "colour"},
  {id: "actions", align: true, disablePadding: false, numeric: 'right', label: "Actions"}
];
const DepartmentTable = () => {

  const [department_open, setDepartmentOpen] = useState(false);
  const [order, setOrder] = useState("asc");
  const [orderBy, setOrderBy] = useState("id");
  const [loader, setLoader] = useState(false);
  const {width} = useSelector(({settings}) => settings);
  const History = useHistory();

  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [tabledata, setTabledata] = useState([]);
  const [Departments, setDepartments] = useState([]);
  const [department_id, setDepartmentId] = useState("");
  const [department_name, setDepartmentName] = useState("");
  const [colour, setColour] = useState("#0000FF");
  const [colorModalOpen, setColorModalOpen] = useState(false);
  const archive_rows = ([
    {id: 0, content: 'Archive'},
    {id: 1, content: 'Live'}
  ]);
  const [goback, setGoback] = useState(false)

  useEffect(() => {
    init_Data();
  }, []);

  const init_Data = () => {
    $http.get(base_url + "department/departmentList")
    .then((response) => {
      setTabledata(response.data.departments);
      setDepartments(response.data.departments);
      setLoader(true);
    })
    .catch((error) => {
      setGoback(true)
    })
  }

  const createSortHandler = property => event => {
    handleRequestSort(event, property);
  };

  const handleRequestSort = (event, property) => {
    let sort_order;
    if (orderBy === property && order === "asc") {
      sort_order = "desc";
    } else {
      sort_order = "asc";
    }

    
    sort_order === "desc"
      ? Departments.sort((a, b) => (b[property] < a[property] ? -1 : 1))
      : Departments.sort((a, b) => (a[property] < b[property] ? -1 : 1));

    
    setOrder(sort_order);
    setOrderBy(property);
  };

  const handleClick = (event, id) => {
    
    //setSelected(newSelected);
  };

  const handleChangePage = (event, page) => {
    setPage(page);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(event.target.value);
  };
  const onSelectDepartment = (data, index) => {
    if (index === 0) {
      History.push({
        pathname: "/app/sites/site",
        state: {id: data}
      })
    } else if (index === 1) {
      DepartmentModalOpen(data);
    } else if (index === 2) {
      const frmData = new FormData();
      frmData.append('id', data);
      $http.post(base_url + "department/deleteDepartment",frmData)
      .then((response) => {
        if (response.data.status === "success") {
            init_Data();
            NotificationManager.info("You removed selected department");
        } else if (response.data.status === "error") {
            NotificationManager.error(response.data.msg, "Error!", 1000, () => {
            });
        }
        
      }).catch((error)=>{
        NotificationManager.error(error, "Error!", 1000, () => {
        });
      })
      if (!navigator.onLine) {
        caches.open('SirvezApp').then(function(cache) {
          cache.match('/api/department/departmentList')
          .then(function(response) {return response ? response.json() : null})
          .then(function(responsedata){
              if(responsedata==null) return
              responsedata.departments = responsedata.departments.filter((item) => {return item.id != data})
              const jsonResponse = new Response(JSON.stringify(responsedata), {
                headers: {'content-type': 'application/json'}
              })
              cache.put('/api/department/departmentList', jsonResponse.clone())
              .then(function(){
                init_Data()
              })
          })
        })
      }
    }
  }

  const SearchDepartment = (data) => {
    setDepartments(
      tabledata.filter(function (item) {
        return (item.department_name.toLowerCase().includes(data.toLowerCase()));
      })
    );
  }
  const DepartmentModalOpen = (data) => {
    setDepartmentId(data);
    setColorModalOpen(false);
    $http.get(base_url + "department/departmentInfo", {params : {id: data}})
    .then((response) => {
        if (data) {
          setDepartmentName(response.data.department.department_name);
          setColour(response.data.department.colour);
        }
        setDepartmentOpen(true);
    })
    .catch((error) => {
      setGoback(true)
    })

  }
  const DepartmentModalClose = () => {
    setDepartmentOpen(false);
  }
  const DepartmentModalSave = () => {
      DepartmentModalClose();
      let offline_id = department_id ? department_id : JSON.parse(localStorage.getItem('user')).id + '_' + new Date().getTime()
      const frmData = new FormData();
      frmData.append('id', offline_id);
      frmData.append('department_name', department_name);
      frmData.append('colour', colour);
      $http.post(base_url + "department/updateDepartment", frmData)
      .then((response) => {
          if (response.data.status === "success") {
              NotificationManager.success("Update Department", "Success!", 1000, () => {
              });
              if(navigator.onLine)
                init_Data();
          } else if (response.data.status === "error") {
              NotificationManager.error(response.data.msg, "Error!", 1000, () => {
              });
          }
      }).catch((error)=>{
        NotificationManager.error(error, "Error!", 1000, () => {
        });
      })
      if (!navigator.onLine) {
        caches.open('SirvezApp').then(function(cache) {
          var modify_data = {
            id : offline_id,
            department_name : department_name,
            colour : colour,
            rooms_count :0,
          }
          cache.match('/api/department/departmentInfo')
          .then(function(response) {return response ? response.json() : null})
          .then(function(responsedata){
              if(responsedata==null) return
              responsedata.department= modify_data
              const jsonResponse = new Response(JSON.stringify(responsedata), {
                headers: {'content-type': 'application/json'}
              })
              cache.put('/api/department/departmentInfo?id='+offline_id, jsonResponse.clone())
             
          })

          cache.match('/api/department/departmentList')
          .then(function(response) {return response ? response.json() : null})
          .then(function(responsedata){
              if(responsedata==null) return
              let item = responsedata.departments.find((item) => {return item.id == offline_id})
              if (item) {
                item.department_name = department_name
                item.colour = colour
              } else {

                  responsedata.departments.unshift(modify_data)
              }
              const jsonResponse = new Response(JSON.stringify(responsedata), {
                headers: {'content-type': 'application/json'}
              })
              cache.put('/api/department/departmentList', jsonResponse.clone())
              .then(function(){
                init_Data()
              })
          })
        })
      }

  }
  const handleRequestClose = (value) => {
    setColour(value.hex);
    setColorModalOpen(false);
  };
  return (
    
        <div>
          <Toolbar className="table-header">
            <div className="title">
              <Typography variant="h6">Departments</Typography>
            </div>
            <div className="col-md-3 col-lg-3 col-sx-6 col-6 ml-auto">
                <SearchBox placeholder="Search ..."
                  onChange={(event) => SearchDepartment(event.target.value)}
                />
            </div>
            <div className="actions">
              <Tooltip title="New Department">
                <IconButton aria-label="New Department" onClick={() => DepartmentModalOpen(null)}>
                    <PlaylistPlayIcon/>
                </IconButton>
              </Tooltip>
            </div>
          </Toolbar>
          {
            loader ? (
              <div className="flex-auto">
                <div className="table-responsive-material">
                  <Table className="">
                    <TableHead>
                      <TableRow>
                        {columnData.map(column => {
                          return (
                            <TableCell
                              key={column.id}
                              align={column.numeric}
                            >
                              <Tooltip
                                title="Sort"
                                placement={column.numeric ? "bottom-end" : "bottom-start"}
                                enterDelay={300}
                              >
                                  <TableSortLabel
                                  active={orderBy === column.id}
                                  direction={order}
                                  onClick={createSortHandler(column.id)}
                                >
                                  {column.label}
                                </TableSortLabel>
                              </Tooltip>
                            </TableCell>
                          );
                        })}
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {
                        Departments.length > 0 ?
                        Departments.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((n) => 
                          <TableRow
                            hover
                            key={n.id}
                            onClick={event => handleClick(event, n.id)}
                            tabIndex={-1}
                          >
                            <TableCell>{n.id}</TableCell>
                            <TableCell>{n.department_name}</TableCell>
                            <TableCell>{n.rooms_count}</TableCell>
                            <TableCell>{n.colour}</TableCell>
                            <TableCell className="text-right">
                              <CardEditMenu key={n.id} selectedData={n.id} 
                                onSelectRow={onSelectDepartment}/>
                            </TableCell>
                          </TableRow>
                        )
                        :
                        <tr>
                            <td className="text-danger" colSpan="5">
                                <IntlMessages id="table.noData"/>
                            </td>
                        </tr>
                      }
                    </TableBody>
                    <TableFooter>
                      <TableRow>
                        <TablePagination
                          count={Departments.length}
                          rowsPerPage={rowsPerPage}
                          page={page}
                          onChangePage={handleChangePage}
                          onChangeRowsPerPage={handleChangeRowsPerPage}
                        />
                      </TableRow>
                    </TableFooter>
                  </Table>
                </div>
                <NotificationContainer/>
              </div>
            ):(
              <div className="loader-view"
                      style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                  <CircularProgress/>
              </div>        
            )
          }
          <Modal key="department-save" className="modal-box" toggle={DepartmentModalClose} isOpen={department_open}>
            <ModalHeader>{department_id ? 'Modify Department':'Add Department'}
                <IconButton className="text-grey"
                            onClick={DepartmentModalClose}>
                    <CloseIcon/>
                </IconButton>
            </ModalHeader>
            <div className="modal-box-content">
                <div className="row">
                    
                    <div className="col-md-12 col-12">
                        <TextField 
                            id="department_name"
                            label="Department"
                            value={department_name}
                            onChange={(event) => setDepartmentName(event.target.value)}
                            margin="normal"
                            helperText="Please Enter Department"
                            fullWidth
                            />
                    </div>
                    <div className="col-md-12 col-12">
                        <TextField
                           
                            InputProps={{
                                style: {
                                    color: colour
                                }
                            }}
                            id="colour"
                            label="Colour"
                            rowsMax="4"
                            value={colour}
                            onFocus = {()=>setColorModalOpen(true)}
                            margin="normal"
                            fullWidth
                        />
                        { colorModalOpen &&
                        <BlockPicker 
                            open = {false}
                            color={colour}
                            colors = {["#f44336", "#e91e63", "#9c27b0", "#3f51b5", "#2196f3", "#03a9f4", "#00bcd4", "#009688", "#4caf50", "#8bc34a", "#cddc39", "#ffc107", "#ff9800", "#ff5722", "#607d8b"]}
                            onChangeComplete={(color)=>handleRequestClose(color)}
                        />
                        }
                    </div>
                </div>
            </div>
            <div className="modal-box-footer d-flex flex-row jr-featured-content-right">
                <Button onClick={DepartmentModalClose} color="secondary" variant="contained" className="jr-btn text-white">
                Cancel
                </Button>
                <Button onClick={DepartmentModalSave} color="primary" variant="contained" className="jr-btn text-white">
                Save
                </Button>
            </div>
          </Modal>
          <SweetAlert show={goback}
              warning
              confirmBtnText="Go Back"
              confirmBtnBsStyle="danger"
              cancelBtnBsStyle="default"
              title="Warning!"
              onConfirm={() => History.go(-1)}
          >                
          Data not cached, so you can not view this page.
          </SweetAlert>

        </div>
        
  );
};

export default DepartmentTable;
